import { Pipe, PipeTransform } from '@angular/core';

import theme from '@vex/utils/tailwindcss';
import Color from 'color';

@Pipe({
  name: 'requestComponentLabel',
})
export class RequestComponentLabelPipe implements PipeTransform {
  labels = {
    headers: {
      text: 'Headers',
      backgroundColor: Color(theme.colors.green['500']).fade(0.9),
      color: theme.colors.green['500'],
    },
    query_params: {
      text: 'Query Params',
      backgroundColor: Color(theme.colors.cyan['500']).fade(0.9),
      color: theme.colors.cyan['500'],
    },
    body_params: {
      text: 'Body Params',
      backgroundColor: Color(theme.colors.teal['500']).fade(0.9),
      color: theme.colors.teal['500'],
    },
    response: {
      text: 'Response',
      backgroundColor: Color(theme.colors.purple['500']).fade(0.9),
      color: theme.colors.purple['500'],
    },
  };

  transform(labels: Array<any>): Array<any> {
    return labels.map(label => {
      return this.labels[label];
    });
  }

}
