import { Injectable } from '@angular/core';
import { ActivatedRouteSnapshot, Resolve } from '@angular/router';

import { BodyParamNameResource } from '@core/http/body-param-name-resource.service';

@Injectable({
  providedIn: 'root',
})
export class BodyParamNamesResolver {
  constructor(
    private bodyParamNameResource: BodyParamNameResource
  ) { }

  resolve(route: ActivatedRouteSnapshot) {
    return this.bodyParamNameResource.index(
      route.params.endpoint_id, {
        project_id: route.queryParams.project_id,
      }
    );
  }
}
