import { Location } from '@angular/common';
import { Component, OnInit } from '@angular/core';
import { MatDialog } from '@angular/material/dialog';
import { ActivatedRoute, ActivatedRouteSnapshot, Router } from '@angular/router';

import icCloudOff from '@iconify/icons-ic/twotone-cloud-off';
import icGroup from '@iconify/icons-ic/twotone-group';
import icMoreVert from '@iconify/icons-ic/twotone-more-vert';
import icPageView from '@iconify/icons-ic/twotone-pageview';
import icTimer from '@iconify/icons-ic/twotone-timer';

import { Breadcrumb } from '@vex/interfaces/breadcrumb.interface';
import { TableColumn } from '@vex/interfaces/table-column.interface';

import { AliasesCreateComponent } from '@aliases/components/aliases-create/aliases-create.component';
import {
  AliasResource,
  BodyParamNameResource,
  HeaderNameResource,
  QueryParamNameResource,
  RequestResource
} from '@core/http';
import { FileService } from '@core/utils/file.service';
import { UriService } from '@core/utils/uri.service';
import {
  Alias, BodyParam, Endpoint, Header, PathSegment, QueryParam, Request, RequestComponentType
} from '@data/schema';

@Component({
  selector: 'vex-endpoint-details',
  templateUrl: './endpoint-details.component.html',
  styleUrls: ['./endpoint-details.component.scss'],
})
export class EndpointDetailsComponent implements OnInit {
  projectId: number;
  endpoint: Endpoint;

  requestTitle = 'Requests';
  requestColumns: TableColumn<Request>[] = [];
  requestData: Request[];

  pathSegmentNameTitle = 'Path Segments';
  pathSegmentNameColumns: TableColumn<PathSegment>[] = [];
  pathSegmentNameData: PathSegment[];

  queryParamNameTitle = 'Query Params';
  queryParamNameColumns: TableColumn<QueryParam>[] = [];
  queryParamNameData: QueryParam[];

  headerNameTitle = 'Headers';
  headerNameColumns: TableColumn<Header>[] = [];
  headerNameData: Header[];

  bodyParamNameTitle = 'Body Params';
  bodyParamNameColumns: TableColumn<BodyParam>[] = [];
  bodyParamNameData: BodyParam[];

  icGroup = icGroup;
  icPageView = icPageView;
  icCloudOff = icCloudOff;
  icTimer = icTimer;
  icMoreVert = icMoreVert;

  crumbs: Breadcrumb[] = [];

  constructor(
    private dialog: MatDialog,
    private route: ActivatedRoute,
    private requestResource: RequestResource,
    private uri: UriService,
    private location: Location,
    private file: FileService,
    private router: Router,
    private aliasResource: AliasResource,
    private headerNameResource: HeaderNameResource,
    private queryParamNameResource: QueryParamNameResource,
    private bodyParamNameResource: BodyParamNameResource
  ) { }

  ngOnInit() {
    const snapshot: ActivatedRouteSnapshot = this.route.snapshot;

    this.projectId = snapshot.queryParams.project_id;
    this.endpoint = new Endpoint(snapshot.data.endpoint);

    this.buildCrumbs();

    this.requestData = snapshot.data.requests.list.map(request => {
      return new Request(request);
    });

    this.headerNameData = snapshot.data.headerNames.map(header => {
      return new Header(header);
    });

    this.queryParamNameData = snapshot.data.queryParamNames.map(queryParam => {
      return new QueryParam(queryParam);
    });

    this.pathSegmentNameData = snapshot.data.pathSegmentNames.map(pathSegment => {
      return new PathSegment(pathSegment);
    });

    this.bodyParamNameData = snapshot.data.bodyParamNames.map(bodyParam => {
      return new BodyParam(bodyParam);
    });

    this.requestColumns = this.buildRequestsTableColumns();
    this.pathSegmentNameColumns = this.buildPathSegmentNameColumns();
    this.headerNameColumns = this.buildComponentNameTableColumns();
    this.queryParamNameColumns = this.buildComponentNameTableColumns();
    this.bodyParamNameColumns = this.buildComponentNameTableColumns();
  }

// API Access

  createOrEditAlias(component, aliasName) {
    const snapshot: ActivatedRouteSnapshot = this.route.snapshot;
    const projectId = snapshot.queryParams.project_id;
    const endpointId = snapshot.params.endpoint_id;

    if (component.alias === undefined) {
      this.aliasResource.create({
        component_name_id: component.id,
        component_type: component.type,
        name: aliasName,
        project_id: projectId,
        endpoint_id: endpointId,
      }).subscribe((res: Alias) => {
        // Once an endpoint has been aliased, a new endpoint is created and the current endopint no longer exists
        const uri = new this.uri.class(this.location.path());
        const path: string = this.file.dirname(uri.pathname);

        uri.pathname = path;

        this.router.navigateByUrl(uri.pathname + uri.query);
      });
    } else {
      this.aliasResource.update(component.alias.id, {
        name: aliasName,
        project_id: projectId,
        endpoint_id: endpointId,
      }).subscribe(res => {
        component.alias.name = aliasName;
      });
    }
  }

  removeAlias(component) {
    const snapshot: ActivatedRouteSnapshot = this.route.snapshot;
    const project_id = snapshot.queryParams.project_id;

    this.aliasResource.destroy(component.alias.id, {
      project_id,
      component_id: component.id,
      component_type: component.type,
    }).subscribe(res => {
      component.alias = null;
      component.aliasName = undefined;
    });
  }

  removeRequestEndpoint(requests: Request[]) {
    requests.forEach(r => {
      this.requestResource.removeEndpoint(r.id).subscribe(res => {
        this.requestData = this.requestData.filter((request: Request) => {
          return request.id !== r.id;
        });
      });
    });
  }

  updateComponentName(component) {
    let resource;

    switch (component.type) {
      case RequestComponentType.Header:
        resource = this.headerNameResource;
        break;
      case RequestComponentType.QueryParam:
        resource = this.queryParamNameResource;
        break;
      case RequestComponentType.BodyParam:
        resource = this.bodyParamNameResource;
        break;
    }

    if (resource) {
      resource.update(this.endpoint.id, component.id, {
        project_id: this.projectId,
        is_required: component.isRequired,
      }).subscribe(res => {

      });
    }
  }

// API Access

  openAliasDialog(component: (BodyParam | QueryParam | Header | PathSegment)) {
    const dialogRef = this.dialog.open(AliasesCreateComponent, {
      width: '600px',
      data: component || {},
    });

    const onCreateSub = dialogRef.componentInstance.onCreate.subscribe(($event) => {
      this.createOrEditAlias(component, $event.name);
    });

    dialogRef.afterClosed().subscribe((request: Request) => {
      onCreateSub.unsubscribe();
    });
  }

// Helpers

  projectIdQuery() {
    return {project_id: this.route.snapshot.queryParams.project_id};
  }

  requestPath(request: Request) {
    return this.file.join(location.pathname, '/requests', request.id);
  }

  buildCrumbs() {
    this.crumbs.push({
      name: 'Endpoints',
      routerLink: ['/endpoints'],
      queryParams: this.route.snapshot.queryParams,
    });
    this.crumbs.push({
      name: this.endpoint.path,
    });
  }

  buildPathSegmentNameColumns(): TableColumn<PathSegment>[] {
    return [
      { label: 'POSITION', property: 'position', type: 'text' },
      { label: 'NAME', property: 'name', type: 'text' },
      { label: '', property: 'link-button', type: 'button', visible: true },
      { label: 'ALIAS', property: 'aliasName', type: 'text', visible: true },
    ];
  }

  buildRequestsTableColumns(): TableColumn<Request>[] {
    return [
      { label: 'Checkbox', property: 'checkbox', type: 'checkbox', visible: true },
      { label: 'METHOD', property: 'method', type: 'text', visible: true },
      { label: 'URL', property: 'url', type: 'link',
        routerLink: (request: Request) => {
          return [this.requestPath(request)];
        },
        queryParams: () => {
          return this.projectIdQuery();
        },
      },
      { label: 'STATUS', property: 'status', type: 'text', visible: true },
      { label: 'LATENCY', property: 'latency', type: 'text', visible: true },
      { label: 'CREATED AT', property: 'createdAt', type: 'text', visible: true },
    ];
  }

  buildComponentNameTableColumns(): TableColumn<any>[] {
    return [
      { label: 'NAME', property: 'name', type: 'text' },
      // { label: '', property: 'link-button', type: 'button', visible: true },
      // { label: 'ALIAS', property: 'aliasName', type: 'text', visible: true }
      { label: 'REQUIRED', property: 'isRequired', type: 'slideToggle', visible: true,
        onclick: (component) => {
          component.isRequired = !component.isRequired;
          this.updateComponentName(component);
        },
      },
    ];
  }
}
