import { Injectable } from '@angular/core';
import { ActivatedRouteSnapshot, Resolve } from '@angular/router';

import { RequestResource } from '@core/http/request-resource.service';

@Injectable({
  providedIn: 'root',
})
export class RequestsResolver {
  constructor(
    private requestResource: RequestResource
  ) { }

  resolve(route: ActivatedRouteSnapshot) {
    return this.requestResource.index({
      endpoint_id: route.params.endpoint_id,
      project_id: route.queryParams.project_id,
    });
  }
}
