import { SelectionModel } from '@angular/cdk/collections';
import { Location } from '@angular/common';
import { AfterViewInit, Component, Input, OnDestroy, OnInit, ViewChild } from '@angular/core';
import { FormControl } from '@angular/forms';
import { MatDialog } from '@angular/material/dialog';
import { MatFormFieldDefaultOptions, MAT_FORM_FIELD_DEFAULT_OPTIONS } from '@angular/material/form-field';
import { MatPaginator } from '@angular/material/paginator';
import { MatSelectChange } from '@angular/material/select';
import { MatSort } from '@angular/material/sort';
import { MatTableDataSource } from '@angular/material/table';
import { ActivatedRoute, ActivatedRouteSnapshot, Router } from '@angular/router';

import { untilDestroyed } from 'ngx-take-until-destroy';
import { filter } from 'rxjs/operators';

import { fadeInUp400ms, stagger40ms } from '@vex/animations';
import { TableColumn } from '@vex/interfaces/table-column.interface';

import { EndpointResource } from '@core/http/endpoint-resource.service';
import { FileService } from '@core/utils/file.service';
import { UriService } from '@core/utils/uri.service';
import { Endpoint, EndpointCategories, Project } from '@data/schema';
import { EndpointsCreateComponent } from '@endpoints/components/endpoints-create/endpoints-create.component';

import { aioTableData, aioTableLabels } from './mock/endpoints-index-data';
import { EndpointsDataService } from './services/endpoints-data.service';
import { EndpointsIndexIcons } from './services/endpoints-index-icons.service';

@Component({
  selector: 'endpoints-index',
  templateUrl: './endpoints-index.component.html',
  styleUrls: ['./endpoints-index.component.scss'],
  animations: [
    fadeInUp400ms,
    stagger40ms,
  ],
  providers: [
    {
      provide: MAT_FORM_FIELD_DEFAULT_OPTIONS,
      useValue: {
        appearance: 'standard',
      } as MatFormFieldDefaultOptions,
    },
  ],
})
export class EndpointsIndexComponent implements OnInit, AfterViewInit, OnDestroy {

  layoutCtrl = new FormControl('boxed');

  // Table settings
  @Input()
  columns: TableColumn<Endpoint>[] = [
    { label: 'Checkbox', property: 'checkbox', type: 'checkbox', visible: true },
    { label: 'Category', property: 'category', type: 'dropdown', visible: true},
    { label: 'Method', property: 'method', type: 'text', visible: true},
    { label: 'Path', property: 'path', type: 'text', visible: true},
    { label: 'Requests', property: 'request_count', type: 'text', visible: true },
  ];
  pageIndex = this.route.snapshot.queryParams.page || 0;
  pageSize = 10;
  pageSizeOptions: number[] = [5, 10, 20, 50];
  dataSource: MatTableDataSource<Endpoint> | null;
  selection = new SelectionModel<Endpoint>(true, []);
  searchCtrl = new FormControl();
  labels = aioTableLabels;

  endpointCategories = EndpointCategories;
  endpointCategoryNames;
  project: Project;

  // Breadcrumb settings
  crumbs = [];

  @ViewChild(MatPaginator, { static: true }) paginator: MatPaginator;
  @ViewChild(MatSort, { static: true }) sort: MatSort;

  constructor(
    public icons: EndpointsIndexIcons,
    private activatedRoute: ActivatedRoute,
    private dialog: MatDialog,
    private endpointsDataService: EndpointsDataService,
    private endpointResource: EndpointResource,
    private file: FileService,
    private location: Location,
    private router: Router,
    private route: ActivatedRoute,
    private uri: UriService
  ) { }

  get visibleColumns() {
    return this.columns.filter(column => column.visible).map(column => column.property);
  }

  ngOnInit() {
    this.endpointsDataService.set(this.route.snapshot.data.endpoints);

    this.dataSource = new MatTableDataSource();
    this.endpointsDataService.endpoints$.pipe(
      filter<Endpoint[]>(Boolean)
    ).subscribe(endpoints => {
      this.dataSource.data = endpoints;
    });

    this.searchCtrl.valueChanges.pipe(
      untilDestroyed(this)
    ).subscribe(value => this.onFilterChange(value));

    const keys = Object.keys(EndpointCategories);
    this.endpointCategoryNames = keys.slice(keys.length / 2);

    this.project = this.route.snapshot.data.project;

    this.crumbs.push({ name: this.project.name });
    this.crumbs.push({ name: 'Endpoints' });
  }

  ngAfterViewInit() {
    this.dataSource.paginator = this.paginator;
    this.dataSource.sort = this.sort;
  }

  openCreateDialog() {
    const dialogRef = this.dialog.open(EndpointsCreateComponent, {
      width: '600px',
    });

    const onCreateSub = dialogRef.componentInstance.onCreate.subscribe(($event) => {
      this.createEndpoint($event);
    });

    dialogRef.afterClosed().subscribe((endpoint: Endpoint) => {
      onCreateSub.unsubscribe();
    });
  }

  getEndpoints(params) {
    this.endpointResource.index(params).subscribe(
      (res: Endpoint[]) => {
        this.endpointsDataService.set(res);
      },
      error => {

      }
    );
  }

  createEndpoint(data: FormData) {
    const snapshot: ActivatedRouteSnapshot = this.route.snapshot;
    const project_id = snapshot.queryParams.project_id;

    data.append('project_id', project_id);

    this.endpointResource.create(data).subscribe(
      res => {
        const curSnapshot: ActivatedRouteSnapshot = this.route.snapshot;

        this.getEndpoints({ project_id: curSnapshot.queryParams.project_id });
      },
      error => {

      });
  }

  showEndpoint(endpoint: Endpoint) {
    const path: string = this.file.join('/endpoints', endpoint.id);
    const snapshot: ActivatedRouteSnapshot = this.route.snapshot;

    this.router.navigate([path], {
       queryParams: { project_id: snapshot.queryParams.project_id },
    });
  }

  deleteEndpoint(endpoint: Endpoint) {
    this.endpointResource.destroy(endpoint.id).subscribe(res => {
      this.endpointsDataService.delete(endpoint.id);
      this.selection.deselect(endpoint);
    });
  }

  deleteEndpoints(endpoints: Endpoint[]) {
    endpoints.forEach(r => this.deleteEndpoint(r));
  }

  updateEndpoint(endpoint: Endpoint, params: object) {
    this.endpointResource.update(endpoint.id, params).subscribe(res => {
      for (const key in params) {
        if (params.hasOwnProperty(key)) {
          endpoint[key] = params[key];
        }
      }
    });
  }

  onFilterChange(value: string) {
    if (!this.dataSource) {
      return;
    }
    value = value.trim();
    value = value.toLowerCase();
    this.dataSource.filter = value;
  }

  toggleColumnVisibility(column, event) {
    event.stopPropagation();
    event.stopImmediatePropagation();
    column.visible = !column.visible;
  }

  /** Whether the number of selected elements matches the total number of rows. */
  isAllSelected() {
    const numSelected = this.selection.selected.length;
    const numRows = this.dataSource.data.length;
    return numSelected === numRows;
  }

  /** Selects all rows if they are not all selected; otherwise clear selection. */
  masterToggle() {
    this.isAllSelected() ?
      this.selection.clear() :
      this.dataSource.data.forEach(row => this.selection.select(row));
  }

  trackByProperty<T>(index: number, column: TableColumn<T>) {
    return column.property;
  }

  onComponentChange(change: MatSelectChange, row: Endpoint) {
    const endpoints = this.endpointsDataService.endpoints;
    const index = endpoints.findIndex(c => c === row);
    endpoints[index].components = change.value;
    this.endpointsDataService.set(endpoints);
  }

  /**
   * On page change, add queryParam 'page' to URL
   */
  onPaginateChange($event) {
    const queryParams = Object.assign({}, this.route.snapshot.queryParams);
    queryParams.page = $event.pageIndex;

    const url = this
        .router
        .createUrlTree([], {relativeTo: this.activatedRoute, queryParams})
        .toString();

    this.location.go(url);
  }

  ngOnDestroy() { }
}
