import { Injectable } from '@angular/core';
import { ActivatedRouteSnapshot, Resolve } from '@angular/router';

import { PathSegmentNameResource } from '@core/http/path-segment-name-resource.service';

@Injectable({
  providedIn: 'root',
})
export class PathSegmentNamesResolver {
  constructor(
    private pathSegmentNameResource: PathSegmentNameResource
  ) { }

  resolve(route: ActivatedRouteSnapshot) {
    return this.pathSegmentNameResource.index(
      route.params.endpoint_id, {
        project_id: route.queryParams.project_id,
      }
    );
  }
}
