import { SelectionModel } from '@angular/cdk/collections';
import { AfterViewInit, Component, EventEmitter, Input, OnChanges, OnInit, Output, SimpleChanges, ViewChild } from '@angular/core';
import { MatPaginator } from '@angular/material/paginator';
import { MatSort } from '@angular/material/sort';
import { MatTableDataSource } from '@angular/material/table';

import icCloudDownload from '@iconify/icons-ic/twotone-cloud-download';
import icCreate from '@iconify/icons-ic/twotone-create';
import icDelete from '@iconify/icons-ic/twotone-delete';
import icLink from '@iconify/icons-ic/twotone-link';
import icMoreHoriz from '@iconify/icons-ic/twotone-more-horiz';

import { TableColumn } from '@vex/interfaces/table-column.interface';
import theme from '@vex/utils/tailwindcss';

@Component({
  selector: 'endpoint-data-table',
  templateUrl: './endpoint-data-table.component.html',
  styleUrls: ['./endpoint-data-table.component.scss'],
})
export class EndpointDataTableComponent<T> implements OnInit, OnChanges, AfterViewInit {
  @Input() title: string;
  @Input() data: T[];
  @Input() columns: TableColumn<T>[];
  @Input() pageSize = 6;

  visibleColumns: Array<keyof T | string>;
  dataSource = new MatTableDataSource<T>();
  selection = new SelectionModel<any>(true, []);

  @ViewChild(MatPaginator, { static: true }) paginator: MatPaginator;
  @ViewChild(MatSort, { static: true }) sort: MatSort;

  icMoreHoriz = icMoreHoriz;
  icCloudDownload = icCloudDownload;
  icLink = icLink;
  icDelete = icDelete;
  icCreate = icCreate;

  theme = theme;

  @Output() selectionDelete = new EventEmitter<Array<any>>();
  @Output() createOrEditAlias = new EventEmitter<any>();
  @Output() removeAlias = new EventEmitter<any>();

  constructor() { }

  ngOnInit() {}

  ngOnChanges(changes: SimpleChanges): void {
    if (changes.columns) {
      this.visibleColumns = this.columns.map(column => column.property);
    }

    if (changes.data) {
      this.dataSource.data = this.data;
    }
  }

  ngAfterViewInit() {
    this.dataSource.paginator = this.paginator;
    this.dataSource.sort = this.sort;
  }

  /** Whether the number of selected elements matches the total number of rows. */
  isAllSelected() {
    const numSelected = this.selection.selected.length;
    const numRows = this.dataSource.data.length;
    return numSelected === numRows;
  }

  /** Selects all rows if they are not all selected; otherwise clear selection. */
  masterToggle() {
    this.isAllSelected() ?
      this.selection.clear() :
      this.dataSource.data.forEach(row => this.selection.select(row));
  }

  deleteSelected() {
    this.selectionDelete.emit(this.selection.selected);
  }
}
