import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';

import { EndpointDetailsComponent } from '@endpoints/pages/endpoint-details/endpoint-details.component';
import { EndpointsIndexComponent } from '@endpoints/pages/endpoints-index/endpoints-index.component';
import { EndpointsResolver } from '@endpoints/pages/endpoints-index/services/endpoints-resolver.service';

import { BodyParamNamesResolver } from '@endpoints/pages/endpoint-details/services/body-param-names-resolver.service';
import { EndpointResolver } from '@endpoints/pages/endpoint-details/services/endpoint-resolver.service';
import { HeaderNamesResolver } from '@endpoints/pages/endpoint-details/services/header-names-resolver.service';
import { PathSegmentNamesResolver } from '@endpoints/pages/endpoint-details/services/path-segment-names-resolver.service';
import { QueryParamNamesResolver } from '@endpoints/pages/endpoint-details/services/query-param-names-resolver.service';
import { RequestsResolver } from '@endpoints/pages/endpoint-details/services/requests-resolver.service';

import { ProjectResolver } from '@projects/services/project-resolver.service';
import { RequestDetailsComponent } from '@requests/pages/request-details/request-details.component';
import { RequestResolver } from '@requests/services/request-resolver.service';
import { ResponseHeadersResolver } from '@requests/services/response-headers-resolver.service';
import { ResponseResolver } from '@requests/services/response-resolver.service';

const routes: Routes = [
  {
    path: '',
    component: EndpointsIndexComponent,
    resolve: {
      endpoints: EndpointsResolver,
      project: ProjectResolver,
    },
  },
  {
    path: ':endpoint_id',
    component: EndpointDetailsComponent,
    resolve: {
      endpoint: EndpointResolver,
      requests: RequestsResolver,
      headerNames: HeaderNamesResolver,
      bodyParamNames: BodyParamNamesResolver,
      queryParamNames: QueryParamNamesResolver,
      pathSegmentNames: PathSegmentNamesResolver,
    },
  },
  {
    path: ':endpoint_id/requests/:request_id',
    component: RequestDetailsComponent,
    resolve: {
      request: RequestResolver,
      response: ResponseResolver,
      responseHeaders: ResponseHeadersResolver,
      parentResource: EndpointResolver,
    },
  },
];

@NgModule({
  imports: [
    RouterModule.forChild(routes),
  ],
  exports: [RouterModule],
})
export class EndpointsRoutingModule { }
