import { Injectable } from '@angular/core';
import { ActivatedRouteSnapshot, Resolve } from '@angular/router';

import { EndpointResource } from '@core/http/endpoint-resource.service';

@Injectable({
  providedIn: 'root',
})
export class EndpointResolver {
  constructor(
    private endpointResource: EndpointResource
  ) { }

  resolve(route: ActivatedRouteSnapshot) {
    return this.endpointResource.show(
      route.params.endpoint_id, {project_id: route.queryParams.project_id}
    );
  }
}
