import { Injectable } from '@angular/core';
import { ActivatedRouteSnapshot, Resolve } from '@angular/router';

import { QueryParamNameResource } from '@core/http/query-param-name-resource.service';

@Injectable({
  providedIn: 'root',
})
export class QueryParamNamesResolver {
  constructor(
    private queryParamNameResource: QueryParamNameResource
  ) { }

  resolve(route: ActivatedRouteSnapshot) {
    return this.queryParamNameResource.index(
      route.params.endpoint_id, {
        project_id: route.queryParams.project_id,
      }
    );
  }
}
