import { Injectable } from '@angular/core';

import { BehaviorSubject } from 'rxjs';

import { Endpoint } from '@data/schema/endpoint';

@Injectable({
  providedIn: 'root',
})
export class EndpointsDataService {
  private subject = new BehaviorSubject(null);
  endpoints$ = this.subject.asObservable();
  endpoints: Endpoint[];

  constructor() { }

  set(endpoints: Endpoint[]) {
    this.endpoints = endpoints;
    this.subject.next(endpoints);
  }

  add(endpoint: Endpoint) {
    this.endpoints.unshift(new Endpoint(endpoint));
    this.set(this.endpoints);
  }

  delete(id: number) {
    this.endpoints.splice(
      this.endpoints.findIndex((existingEndpoint) => {
        return existingEndpoint.id === id;
      }), 1);

    this.set(this.endpoints);
  }
}
