import { Injectable } from '@angular/core';
import { ActivatedRouteSnapshot, Resolve } from '@angular/router';

import { HeaderNameResource } from '@core/http/header-name-resource.service';

@Injectable({
  providedIn: 'root',
})
export class HeaderNamesResolver {
  constructor(
    private headerNameResource: HeaderNameResource
  ) { }

  resolve(route: ActivatedRouteSnapshot) {
    return this.headerNameResource.index(
      route.params.endpoint_id, {
        project_id: route.queryParams.project_id,
      }
    );
  }
}
