import { Component, EventEmitter, Inject, OnInit } from '@angular/core';
import { FormArray, FormBuilder, FormControl, FormGroup, Validators } from '@angular/forms';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';

import { debounceTime } from 'rxjs/operators';

import icClose from '@iconify/icons-ic/twotone-close';
import icDownload from '@iconify/icons-ic/twotone-cloud-download';
import icDelete from '@iconify/icons-ic/twotone-delete';
import icEditLocation from '@iconify/icons-ic/twotone-edit-location';
import icLocationCity from '@iconify/icons-ic/twotone-location-city';
import icMoreVert from '@iconify/icons-ic/twotone-more-vert';
import icMyLocation from '@iconify/icons-ic/twotone-my-location';
import icPerson from '@iconify/icons-ic/twotone-person';
import icPhone from '@iconify/icons-ic/twotone-phone';
import icPrint from '@iconify/icons-ic/twotone-print';

@Component({
  selector: 'endpoints-create',
  templateUrl: './endpoints-create.component.html',
  styleUrls: ['./endpoints-create.component.scss'],
})
export class EndpointsCreateComponent implements OnInit {

  onCreate = new EventEmitter<object>();

  form: FormGroup;
  formOptions = {
    methods: ['GET', 'OPTIONS', 'POST', 'PUT', 'DELETE'],
    pathSegmentTypes: ['Static', 'Alias'],
  };

  icMoreVert = icMoreVert;
  icClose = icClose;

  icPrint = icPrint;
  icDownload = icDownload;
  icDelete = icDelete;

  icPerson = icPerson;
  icMyLocation = icMyLocation;
  icLocationCity = icLocationCity;
  icEditLocation = icEditLocation;
  icPhone = icPhone;

  constructor(
    @Inject(MAT_DIALOG_DATA) public defaults: any,
    private dialogRef: MatDialogRef<EndpointsCreateComponent>,
    private fb: FormBuilder
  ) { }

  ngOnInit() {
    const pathControl: FormControl = new FormControl('', [Validators.required]);

    pathControl.valueChanges.pipe(
      debounceTime(500)
    ).subscribe(path => {
      this.renderPath(path);
    });

    this.form = this.fb.group({
      host: new FormControl('', [Validators.required]),
      method: new FormControl('', [Validators.required]),
      path: pathControl,
      pathSegments: this.fb.array([]),
      port: new FormControl('', [
        Validators.required, Validators.pattern('^[0-9]*$'), Validators.minLength(1), Validators.maxLength(5),
      ]),
    });
  }

  create() {
    const form = this.form.value;
    const formData = new FormData();

    formData.append('host', form.host);
    formData.append('method', form.method);
    formData.append('path', form.path);
    formData.append('path_segments', JSON.stringify(form.pathSegments));
    formData.append('port', form.port);

    this.onCreate.emit(formData);
    this.dialogRef.close();
  }

// Helpers

  renderPath(path: string) {
    const pathSegmentStrings = path.split('/').filter(s => s.length > 0);
    const pathSegments: FormArray = this.form.get('pathSegments') as FormArray;

    let i = 0;
    for (const control of pathSegments.controls) {
      if (i === pathSegmentStrings.length) { break; }

      const pathSegmentName = pathSegmentStrings[i];

      if (pathSegmentName.length && control.value.name !== pathSegmentName) {
        control.patchValue({
          name: pathSegmentName,
        });
      }

      i += 1;
    }

    if (pathSegmentStrings.length > pathSegments.length) {
      for (let i = pathSegments.length; i < pathSegmentStrings.length; ++i) {
        const pathSegmentName = pathSegmentStrings[i];
        if (pathSegmentName.length === 0) { continue; }

        const formControl: FormGroup = this.createPathSegmentFormGroup(pathSegmentName);
        pathSegments.push(formControl);
      }
    } else if (pathSegmentStrings.length < pathSegments.length) {
      for (let i = pathSegmentStrings.length; i < pathSegments.length; ++i) {
        pathSegments.removeAt(i);
      }
    }
  }

  renderPathSegments() {
    const pathSegmentStrings = [];

    const pathSegments: FormArray = this.form.get('pathSegments') as FormArray;
    for (const control of pathSegments.controls) {
      pathSegmentStrings.push(control.value.name);
    }

    this.form.patchValue({
      path: `/${pathSegmentStrings.join('/')}`,
    });
  }

  createPathSegmentFormGroup(pathSegmentName: string): FormGroup {
    const pathSegmentNameControl = new FormControl(pathSegmentName, [Validators.required]);

    pathSegmentNameControl.valueChanges.pipe(
      debounceTime(500)
    ).subscribe(() => {
      this.renderPathSegments();
    });

    return this.fb.group({
      name: pathSegmentNameControl,
      type: new FormControl(this.formOptions.pathSegmentTypes[0], [Validators.required]),
    });
  }
}
